.containerr{
    margin-top: 450px;
    
    width: 250px;
    height: auto;
    text-align: center;
    background-color: white;
    border-radius: 3px;

}
.containerr h1{
    text-align: left;
    padding: 10px;
    font-size: 16px;
    background-color: #eee;
    color: #333;
    letter-spacing: 3px;

}
.containerr ul li{
    list-style: none;
}
.containerr ul li a{
    display: block;
    padding: 10px 0;
    text-decoration: none;
    font-weight: 700;
    color: #333;
    background-color: white;
    font-size: 18px;
    text-transform: uppercase;
    transition: .1s;
    border: 1px solid #eee;
}
.containerr:hover ul li a{
    color: rgb(185, 180, 180);
}

.containerr ul li a:hover{
    color: rgb(0, 255, 242);
    transform: scale(1.1);
    background-color: violet;
    border: 1px solid violet;
}

.containerr ul li a:hover::before{
    content: '';
    width: 0;
    height: 0;
    position: absolute;
    left: -1.5px;
    top: -12.5px;
    border-style: solid ;
    border-width: 0 0 11.5px 11.5px;
    border-color: transparent transparent rgb(240, 114, 240) transparent;
}

.containerr ul li a:hover::after{
    content: '';
    width: 0;
    height: 0;
    position: absolute;
    right: -1.5px;
    top: -12.5px;
    border-style: solid ;
    border-width: 11.5px 0 0 11.5px;
    border-color: transparent transparent transparent rgb(240, 114, 240);
}


